import React,{ Component } from "react";
import { Link } from "react-router-dom";
import classnames from "classnames";
const isDeploy = true;
const addPathPrerix='/portfolio';
const rootPath = isDeploy?addPathPrerix+'/':'';
const ttfPath = isDeploy?addPathPrerix+'/TTFConverter':'/TTFConverter';
const setoPath = isDeploy?addPathPrerix+'/setogreenEstate':'/setogreenEstate';
const shimogamoPath = isDeploy?addPathPrerix+'/shimogamodeli':'/shimogamodeli';
const chronographPath = isDeploy?addPathPrerix+'/chronograph':'/chronograph';
const dashboardPath = isDeploy?addPathPrerix+'/dashboard':'/dashboard';
export default class Header extends Component{
  constructor(props){
    super(props);
    this.scrollHandler = this.scrollHandler.bind(this);
    this.state = {
      hide:this.props.hide,
      show:false
    }
  }
  scrollHandler(){
    const result = window.scrollY>0?true:false;
    this.setState({'show':result});
  }
  componentDidMount(){
    window.addEventListener('scroll',this.scrollHandler,true);
  }
  componentWillUnmount(){
    window.removeEventListener('scroll',this.scrollHandler);
  }

  render(){
    const headerClass = classnames(
      'root_header',
      {
        'header_show':this.state.show
      }
    )
    return(
      <div className={headerClass}>
        <ul className='header_link_container'>
          <PerformanceLink
            path={rootPath}
            title='Top'
            hide={this.props.hide}
          />
          <PerformanceLink
            path={shimogamoPath}
            title=''
            hide={this.props.hide}
          />
          <PerformanceLink
            path={ttfPath}
            title='Web'
            hide={this.props.hide}
          />
          <PerformanceLink
            path={setoPath}
            title=''
            hide={this.props.hide}
          />
          <PerformanceLink
            path={chronographPath}
            title='3D'
            hide={this.props.hide}
          />
          <PerformanceLink
            path={dashboardPath}
            title=''
            hide={this.props.hide}
          />
        </ul>
      </div>
    )
  }
}

class PerformanceLink extends Component{
  render(){
    const headerLink = classnames(
      'header_link',
      {
        'selected':this.props.hide==this.props.path
      }
    )
    return(
      <li className={headerLink}>
        <Link to={this.props.path}>{this.props.title}</Link>
      </li> 
    )
  }
}


